<template>
  <div>
    <div class="page-title">
      <h3>Планирование</h3>
      <h4>{{ info.bill | currencyFilter('RUB') }}</h4>
    </div>
    <loader
      v-if="loading"
    />
    <p
    v-else-if="!categories.length"
    >
      Категорий пока нет.
      <router-link to="/categories">
        Добавить новую категорию.
      </router-link>
    </p>
    <section v-else>
      <div v-for="cat in categories"
           :key="cat.id">
        <p>
          <strong>
            {{ cat.title }}:
          </strong>
          {{ cat.spend  | currencyFilter('RUB') }} из {{ cat.limit  | currencyFilter('RUB') }}
        </p>
        <div class="progress" v-tooltip="cat.tooltip">
          <div
            class="determinate"
            :class="[cat.progressColor]"
            :style="{width: cat.progressPercent + '%'}"
          ></div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'
import currencyFilter from '../filters/CurrencyFilter'
export default {
  name: 'Planing',
  metaInfo () {
    return {
      title: this.$title('Menu_Planning')
    }
  },
  data: () => ({
    categories: [],
    loading: true,
    records: []
  }),
  computed: {
    ...mapGetters(['info'])
  },
  async mounted () {
    // const records = await this.$store.dispatch('fetchRecords')
    const categories = await this.$store.dispatch('fetchCategories')
    for (let i = 0; i < categories.length; i++) {
      const catId = (categories[i]).id
      const recordsArray = await this.$store.dispatch('fetchRecords', catId)

      this.records = this.records.concat(recordsArray)
    }

    this.categories = categories.map(cat => {
      const spend = this.records
        .filter(r => r.categoryId === cat.id)
        .filter(r => r.type === 'outcome')
        .reduce((total, record) => {
          total += record.amount
          return total
        }, 0)

      const percent = 100 * spend / cat.limit
      const progressPercent = percent > 100 ? 100 : percent
      const progressColor = percent < 60
        ? 'green'
        : percent < 100
          ? 'yellow'
          : 'red'

      const tooltipValue = cat.limit - spend
      const tooltip = `${tooltipValue < 0 ? 'Превышение на' : 'Осталось'} ${currencyFilter(Math.abs(tooltipValue))}`
      return {
        ...cat,
        progressPercent,
        progressColor,
        spend,
        tooltip
      }
    })
    this.loading = false
  }
}
</script>
